import React, { useMemo, useState } from "react";
import Button from "../../components/Button";
import { Container } from "./styled";

const slowOperation = () => {
  let slow;
  for (let index = 0; index < 100000000; index++) {
    slow = index + index / 10;
  }
  return slow;
};

const UseMemo = () => {
  const [count, setCount] = useState(0);

  const value = useMemo(() => {
    // useMemo ir armazenar um dado na memoria,  muito utilizado para operaes lentas
    const localStorage = window.localStorage.getItem("Produto");
    console.log("Ocorreu a operao");
    return localStorage;
  }, []); // array de dependencias mesmo metodo do useEffect

  const returnSlowValue = slowOperation();

  const getPerformance = performance.now(); // performance.now ir retornar uma temporizador de onde est o meu app neste momento aqui agora

  const useMemoValueSlow = useMemo(() => returnSlowValue, []); // aos o valor guardado na memoria a operao ir ocorrer mais rapidamentes

  // console.log(returnSlowValue);
  // console.log(value);
  console.log(performance.now() - getPerformance); // ir comparar os tempos em ms

  return (
    <Container>
      <Button
        onClick={() => setCount(count + 1)}
        margin="30px"
        width="100px"
        height="40px"
      >
        UseMemo {count}
      </Button>
    </Container>
  );
};

export default UseMemo;
